<!-- 医废装箱 -->
<template>
  <div>
    <Row :gutter="20">
      <Col :sm="6" :lg="4">
        <Input v-model="barCode" placeholder="打印编码"></Input>
      </Col>
      <Col :sm="6" :lg="4">
        <Select v-model="type">
          <Option
            v-for="item in typeList"
            :value="item.value"
            :key="item.value"
            :label="item.label"
          ></Option>
        </Select>
      </Col>
      <Col :sm="6" :lg="4">
        <Date-picker type="daterange" format="yyyy/MM/dd" placeholder="选择日期和时间"></Date-picker>
      </Col>
      <Col :sm="6" :lg="4">
        <Button type="success" @click="previewModal = true">
          <Icon type="md-search" />查询
        </Button>
      </Col>
    </Row>
    <br />
    <Row>
      <Button type="error">
        <Icon type="md-close" />删除
      </Button>
    </Row>
    <br />
    <Card>
      <Table :columns="column1" :data="data1"></Table>
      <br />
      <Page :total="100" />
    </Card>

    <Modal v-model="previewModal" title="查看医废装箱">
      <Form ref="formItem" :model="formItem" inline>
        <FormItem label="批号">
          <Input v-model="formItem.No" disabled></Input>
        </FormItem>
        <FormItem label="打印编号">
          <Input v-model="formItem.No" disabled></Input>
        </FormItem>
        <FormItem label="装箱编号">
          <Input v-model="formItem.No" disabled></Input>
        </FormItem>
        <FormItem label="类型">
          <Input v-model="formItem.No" disabled></Input>
        </FormItem>
        <FormItem label="毛重">
          <Input v-model="formItem.No" disabled></Input>
        </FormItem>
        <FormItem label="净重">
          <Input v-model="formItem.No" disabled></Input>
        </FormItem>
        <FormItem label="装桶状态">
          <Input v-model="formItem.No" disabled></Input>
        </FormItem>
        <FormItem label="装桶地点">
          <Input v-model="formItem.No" disabled></Input>
        </FormItem>
        <FormItem label="时间">
          <Input v-model="formItem.No" disabled></Input>
        </FormItem>
        <FormItem label="装桶人">
          <Input v-model="formItem.No" disabled></Input>
        </FormItem>
        <FormItem label="所属部门">
          <Input v-model="formItem.No" disabled></Input>
        </FormItem>
        <FormItem label="所属机构">
          <Input v-model="formItem.No" disabled></Input>
        </FormItem>
        <br />装箱明细（条码+重量）
        <hr />
        <Row>
          <Col span="12">
            <CellGroup>
              <Cell title="Display right content">
                <Tag slot="extra" color="primary">primary</Tag>
              </Cell>
            </CellGroup>
          </Col>
          <Col span="12">
            <CellGroup>
              <Cell title="Display right content">
                <Tag slot="extra" color="primary">primary</Tag>
              </Cell>
            </CellGroup>
          </Col>
        </Row>
      </Form>
    </Modal>
  </div>
</template>

<script>
export default {
  name: "WasteContainer",
  data() {
    return {
      barCode: "",
      type: "1",
      state: "1",
      previewModal: false,
      column1: [
        {
          key: "select",
          type: "selection",
          width: 60
        },
        {
          title: "打印编号",
          key: "printNo"
        },
        {
          title: "类型",
          key: "wasteType"
        },
        {
          title: "净重",
          key: "kg"
        },
        {
          title: "数量",
          key: "num"
        },
        {
          title: "装桶状态",
          key: "state"
        },
        {
          title: "时间",
          key: "time"
        },
        {
          title: "装桶人",
          key: "barrelPerson"
        },
        {
          title: "所属部门",
          key: "departmentName"
        },
        {
          title: "所属机构",
          key: "orgName"
        },
        {
          title: "操作",
          render: h => {
            return h(
              "Button",
              {
                props: {
                  type: "success",
                  size: "small",
                  icon: "md-create"
                }
              },
              "查看"
            );
          }
        }
      ],
      data1: [],
      typeList: [
        {
          value: "1",
          label: "不限"
        },
        {
          value: "2",
          label: "未出库"
        },
        {
          value: "3",
          label: "已出库"
        }
      ],
      formItem: {
        No: ""
      }
    };
  }
};
</script>